@font-face {
    font-family: 'Hejar';
    font-size: 1.5rem !important;
    src: url('./fonts/KHejar.eot'); /* IE9 Compat Modes */
    src: url('./fonts/KHejar.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */ url('./fonts/KHejar.woff') format('woff2'), /* Super Modern Browsers */ url('./fonts/KHejar.woff') format('woff'), /* Pretty Modern Browsers */ url('./fonts/KHejar.ttf') format('truetype'), /* Safari, Android, iOS */ url('./fonts/KHejar.svg#svgFontName') format('svg'); /* Legacy iOS */
}

body {
    margin: 0;
    font-family: "Roboto Thin", serif !important;
    font-size: large !important;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}